.site-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include container-margins;
  margin-bottom: 1.2rem;
    &__logo {
        img {
            display: block;
            height: 7rem;   
        }
    }
    ul {
      display: flex;
      li {
        margin-right: 1rem;
        a {
          color: $blue;
          text-transform: uppercase;
          letter-spacing: 1px;
          transition: all 300ms ease-in;
          &:hover {
            color: $red;
          }
        }
      }
    }
    &__misc {
      display: flex;
    }
    .search-btn {
      height: 3rem;
      cursor: pointer;
      margin-left: 4rem;
    }
    &__book-apt-cta {
      display: inline-block;
      background-color: $red;
      color: $white;
      padding: 0.8rem 1.5rem;
    }
}


